import React, { useState } from "react";
import { data } from "../utils/data";
import { IoMdArrowDropdown, IoMdArrowDropup } from 'react-icons/io'

export const NetworkTab = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [selectedChain, setSelectedChain] = useState(null);




    const getActiveChain = (id) => {
        const chain = data.find((d) => d.chainId === id);
        setSelectedChain(chain);
    };

    // const filteringChain =(id)=>{
    //     id ==
    // }



    return (
        <div className="w-1/6">

            <div className="relative flex flex-col">
                <button onClick={() => setIsOpen(!isOpen)} className=" w-full p-2 items-center  flex justify-between  border-black font-bold tracking-wider border focus:border-black">
                    <span>
                        {selectedChain && (
                            <img
                                src={selectedChain.icon}
                                className="rounded-full"
                                alt="icon"
                                height="30"
                                width="30"
                            />
                        )}
                        {selectedChain?.name}
                    </span>
                    {
                        !isOpen ? <IoMdArrowDropdown /> : <IoMdArrowDropup />
                    }
                </button>

                <select className="border-black flex flex-col items-start rounded-lg p-2 w-full ">

                    {
                        data.filter((colos)=> console.log(colos))
                    }
                    {
                        isOpen && (data
                            // .filter((c) => c.chainId !== selectedChain.chainId)
                            // .filter((c) => c.chainId !== selectedChain.chainId)
                            .map((item, i) => {
                                return (
                                    <button
                                        key={i}
                                        // onClick={() => {
                                        //     setSelectedChain(!selectedChain);
                                        //     getActiveChain(d.chainId);
                                        // }}
                                        className="flex w-full justify-around  hover:bg-blue-300 p-3 cursor-pointer  hover:border-l-white border-l-4"
                                    >
                                        <img
                                            src={item?.icon}
                                            className="rounded-full"
                                            alt="icon"
                                            height="30"
                                            width="30" /> {item?.name}

                                    </button>
                                )
                            })
                        )
                    }

                </select>
            </div>



        </div>
    )
}



